.btns {
  position: absolute;
  right: 10%;
  width: 50%;
  top: 0.5%;
  height: fit-content;
  display: flex;
  justify-content: space-between;
  button {
    cursor: pointer;
    width: 30%;
    color: rgba(191, 191, 191, 1);
    background-color: #fff;
    font-size: 1.7vmax;
    line-height: 1.5;
    border: 5px solid rgba(191, 191, 191, 1);
    border-radius: 50px;
    font-weight: bold;
    text-transform: uppercase;
    transition: 0.5s ease;
    &:last-child {
      width: 20%;
      color: black;
      border: 5px solid rgba(191, 191, 191, 1);
      &.clickCart {
        border: 5px solid rgba(255, 122, 0, 1);
      }
    }
    &.click {
      color: #ff9330;
      border: 5px solid #ff9330;
    }
  }
}
.backBtn {
  position: absolute;
  left: 0;
  top: 40%;
  font-size: 5vmax;
  transform: rotate(180deg);
  color: red;
  cursor: pointer;
}
#menuWrap {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: repeat(10, 30%);
  grid-template-rows: 100%;
  .cardWrap {
    opacity: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    width: 65%;
    height: 100%;
    background: url("../../public/images/menu_card.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 100% 75%;
    h3 {
      z-index: 6;
      width: 100%;
      text-align: center;
      position: absolute;
      top: 5%;
      font-size: 2vmax;
      font-family: "Bangers", cursive;
    }
    .imge {
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      top: 10%;
      width: 100%;
      height: 30%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    .foodname {
      z-index: 4;
      margin: 0 5%;
      margin-top: -15%;
      -webkit-text-stroke: 2px #000000;
      text-shadow: 8px 7px 0px rgba(0, 0, 0, 0.2);
      font-size: 2.5vmax;
      color: white;
      font-weight: bold;
      text-transform: uppercase;
    }
    .price {
      text-align: center;
      border-radius: 50%;
      height: fit-content;
      width: fit-content;
      background-color: rgba(255, 238, 0, 0.712);
      position: absolute;
      top: 45%;
      right: 5%;
      font-weight: bold;
      font-size: 1.5vmax;
    }

    .toppings {
      position: absolute;
      bottom: 25%;
      width: 100%;
      height: 20%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: repeat(4, 1fr);
      place-items: center;
      row-gap: 5%;
      &.prevent {
        pointer-events: none;
        text-decoration: line-through solid 3px rgb(0, 0, 0);
      }
      h6 {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 1.1vmax;
      }
      div {
        display: grid;
        place-items: center;
        width: 100%;
        height: 100%;
        label {
          cursor: pointer;
          color: #a0a0a0;
          font-size: 1.2vmax;
          text-transform: capitalize;
          text-align: center;
          border-radius: 1rem;
          width: 70%;
          border: 2.5px solid #a0a0a0;
          line-height: 1.5;
        }
        input {
          width: 0px;
          height: 0px;
          padding: 0px;
          margin: 0px;
          display: none;
          &:checked + label {
            color: rgba(255, 122, 0, 1);
            border: 2.5px solid rgba(255, 122, 0, 1);
            box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
          }
        }
      }
    }
    .quantityBox {
      position: absolute;
      bottom: 14%;
      width: 40%;
      display: flex;
      justify-content: center;
      align-items: center;
      left: 50%;
      transform: translateX(-50%);
      span {
        line-height: 1.3;
        width: 45%;
        font-size: 1.3vmax;
        border-radius: 50px;
        cursor: pointer;
      }
      #qty {
        text-align: center;
        z-index: 3;
        width: 40%;
        background: #ffd94a;
        font-size: 1.7vmax;
      }
      #minus {
        padding: 1% 0 1% 10%;
        position: absolute;
        left: 0;
        background: #ff8888;
      }
      #plus {
        text-align: right;
        position: absolute;
        right: 0;
        padding: 1% 10% 1% 0;
        background: #92d791;
      }
    }
    button {
      cursor: pointer;
      color: rgba(255, 122, 0, 1);
      border: none;
      width: 100%;
      line-height: 1.7;
      font-size: 1.5vmax;
      font-weight: bold;
      border-radius: 50px;
      position: absolute;
      bottom: 5%;
      background: #ffd94a;
      box-shadow: 0em 0.3em 0 0em rgba(255, 122, 0, 1);
    }
  }
}
