#body__wrap {
  width: 100%;
  height: 100vh;
  display: grid;
  #bg {
    display: flex;
    background-color: white;
    width: 90%;
    height: 85%;
    margin: auto;
    box-shadow: 1em 1em 0px rgba(0, 0, 0, 0.16);
    border-radius: 50px;
    overflow-x: scroll;
    overflow-y: hidden;
    &::-webkit-scrollbar {
      display: none;
    }
    h1 {
      z-index: 5;
      letter-spacing: -0.02em;
      font-family: "Bangers", cursive;
      font-size: 5vmax;
      position: absolute;
      top: 0;
      left: 10%;
      color: #ff0707;
      -webkit-text-stroke: black 0.04em;
      text-shadow: 0.07em 0.07em 0 rgba(0, 0, 0, 0.25);
    }
  }
}
