.recipt {
  position: absolute;
  text-transform: uppercase;
  top: 7.5%;
  right: 5%;
  height: 85%;
  width: 40%;
  text-align: center;
  background: #f5f5e6;
  overflow-y: scroll;
  border-radius: 50px;
  &::-webkit-scrollbar {
    display: none;
  }
  .total {
    line-height: 2;
    font-size: 2vmax;
    font-weight: bold;
    border-bottom: 3px dashed rgba(0, 0, 0, 0.3);
  }
}
.reciptWrap {
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.myFood {
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  padding: 3%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  .index {
    color: red;
    font-weight: bold;
  }
  div {
    width: 50%;
    text-align: left;
  }
  .qty,
  .price {
    text-align: right;
    width: auto;
    line-height: 1.5;
  }
  .topping {
    font-size: 0.8vmax;
  }
  .delete {
    cursor: pointer;
    transition: 0.5s ease;
    width: 5%;
    height: auto;
    &:hover {
      transform: scale(1.2);
    }
  }
}
