@import url("https://fonts.googleapis.com/css2?family=Bangers&display=swap");


.choose__store {
  display: grid;
  place-items: center;
  position: relative;
  width: 50%;
  height: 100%;
  text-transform: uppercase;
  button {
    text-transform: uppercase;
    position: absolute;
    bottom: 20%;
    right: 20%;
    padding: 0 5%;
    font-weight: bold;
    font-size: 2.5vmax;
    line-height: 1.5;
    color: #000000;
    background: #ff9330;
    border: none;
    border-radius: 50px;
    box-shadow: 0.25em 0.25em 0 0em black;
  }
  .select {
    margin-bottom: 10%;
  }
}

.circle {
  position: relative;
  margin: auto 5%;
  width: 50%;
  height: 80%;
  background-image: url("../../public/images/circle.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  &::after {
    mix-blend-mode: multiply;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-image: url("../../public/images/circle_color.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    animation: move 7s linear 0s infinite alternate;
  }
  img {
    opacity: 0;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    width: 70%;
  }
}
@keyframes move {
  0% {
    transform: rotate(0deg) translate(0, 0);
  }
  50% {
    transform: rotate(-190deg) translate(-5%, 5%);
  }
  100% {
    transform: rotate(-360deg) translate(0, 0);
  }
}
